html {
  box-sizing: border-box;
  font-size: 10px;
}

* {
  font-family: 'Lato', sans-serif;
  margin: 0;
  padding: 0;
}

p,
a,
span,
button,
li {
  font-size: 1.4rem;
  line-height: 2rem;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#map,
#root {
  width: 100vw;
  height: 100vh;
}

#map {
  position: absolute;
  top: 0;
}

#root {
  display: flex;
  justify-content: center;
}
